.tile {
  display: block;
  min-height: 8rem;
  padding: 0.5rem;
  color: $color-black;
  background-color: $color-light;
  text-decoration: none;
  transition: 0.25s background-color ease-in-out;
  &:hover {
    background-color: darken($color-light, 5%);
  }
  @include portrait {
    display: flex;
    align-items: center;
    min-height: 5rem;
  }
  &__heading {
    @include ellipsis(nowrap, hidden, ellipsis);
    margin: 0;
    font-size: 1.3rem;
    font-weight: 500;
    text-transform: uppercase;
    @include portrait {
      @include ellipsis(pre-wrap, visible, unset);
      @include line-clamp(-webkit-box, 2, vertical, hidden);
      margin-right: 1rem;
    }
  }
  &__inner {
    display: flex;
    align-items: flex-start;
  }
  &__img {
    width: 4.5rem;
    height: 5.3rem;
    flex-shrink: 0;
    img {
      height: 100%;
      object-fit: cover;
    }
    @include portrait {
      display: none;
    }
  }
  &__text {
    @include line-clamp(-webkit-box, 3, vertical, hidden);
  }
  &__desc {
    margin: 0 0 0 0.5rem;
    font-size: 1.3rem;
    line-height: 1.6rem;
    color: $color-dark;
    @include portrait {
      display: none;
    }
  }
  &__link-label {
    display: none;
    font-size: 1.4rem;
    @include portrait {
      @include line-clamp(-webkit-box, 2, vertical, hidden);
      min-width: 5rem;
      margin-left: auto;
      text-align: right;
      font-size: 1.2rem;
    }
  }
}
